          {fonts: [{ text: '$250 Walmart', top: '18%' ,fontSize:'16px'}], background: 'rgba(255, 237, 225, 1)'},
          {fonts: [{ text: '$750 Walmart', top: '18%' ,fontSize:'16px'}], background: 'rgba(255, 249, 242, 1)'},
          {fonts: [{ text: '$500 Walmart', top: '18%' ,fontSize:'16px'}], background: 'rgba(255, 237, 225, 1)'},
          {fonts: [{ text: '$250 Walmart', top: '18%' ,fontSize:'16px'}], background: 'rgba(255, 249, 242, 1)'},
          {fonts: [{ text: '$500 Walmart', top: '18%' ,fontSize:'16px'}], background: 'rgba(255, 237, 225, 1)'},
          {fonts: [{ text: '$1000 Walmart', top: '18%',fontSize:'16px' }], background: 'rgba(255, 249, 242, 1)'},
          {fonts: [{ text: '$500 Walmart', top: '18%' ,fontSize:'16px'}], background: 'rgba(255, 237, 225, 1)'},
          {fonts: [{ text: '$1000 Walmart', top: '18%',fontSize:'16px' }], background: 'rgba(255, 249, 242, 1)'},
        ],
        buttons: [
          { radius: '50px', background: '#fff' },
          { radius: '45px', background: 'RGBA(241, 231, 224, 1)' },
          {
            radius: '40px', background: 'rgba(248, 88, 5, 1)',
            pointer: true,
            fonts: [{ text: 'SPIN\nNOW!', top: '-20px',fontColor:'#fff' }]
          },
        ],
        countdown:300,
        countdowntwo:120,
        setcount:8
      };
    },
    computed: {
       
    },
    created(){
    },
    mounted:function () {
      this.getJSON()
    },
    methods: {
      getRandomData() {
        return Math.floor(Math.random() * 41000) + 10000; // 10秒到50秒（10000ms到50000ms）
      },
        // 递减操作，直到剩余2
      decrementCount() {
          if (this.setcount > 2) {
            this.setcount--; // 减少数量
            // 如果还有次数需要减少，继续设置下一个递减时间
            this.intervalId = setTimeout(this.decrementCount, this.getRandomData());
          } else {
            // 当剩余数量为2时，清除定时器
            clearTimeout(this.intervalId);
          }
      },
      secondsToMMSS(seconds) {
          if(typeof seconds === 'string' ){
            return seconds
          }
          const minutes = Math.floor(seconds / 60);
          const secondsPart = seconds % 60;
          return `${minutes.toString().padStart(2, '0')}:${secondsPart.toString().padStart(2, '0')}`;
      },
      startCountdown(item) {
        setTimeout(() => {
          if (this[item] > 0) {
            this[item] -= 1;
            this.startCountdown(item); // 递归调用自身，实现倒计时效果
          } else {
            // 倒计时结束后的操作
            if(item == 'countdown'){
              this[item] = 'expired'
            }
          }
        }, 1000); // 每1000毫秒（1秒）更新一次
      },
      getRegionFromLanguageTag(tag) {
        if(tag.includes('-')){
          const parts = tag.split('-');
          if (parts.length > 1) {
              return parts[0]; // 第二个部分通常是地区代码
          }
        }
        return tag||'en'; // 如果没有地区代码，返回null或适当的默认值
      },
      getJSON(){
        // var lang = this.getQueryVariable('lang')||'en'
        var fn = this.getQueryVariable('fn')||'1'
        const region = this.getRegionFromLanguageTag(navigator.language).toLowerCase()
        console.log(region,'当前获取到的语言')
        // ../../../static/menu.json
        axios({
          method:'get',
          url:`./static/menu_${fn}.json`,
          dataType:'json',
          crossDomain:true,
          cache:false
        }).then((res)=>{
          let lang = 'en'
          this.pageData = res.data[lang];
          this.styleType = res.data[lang].styleType
          if(this.styleType==1){
            this.commentGun()
          }else{
            this.startCountdown('countdown')
            this.decrementCount();
          }
        })
      },
      commentGun(){
        setInterval(() => {
          this.currentIndex++; // 增加索引
            if (this.currentIndex >= this.commentData.length) { // 如果超出数组长度，重置索引到0
              this.currentIndex = 0;
            }
        }, 5000); // 每5秒更新一次
      },
      getQueryVariable(item)
      {
          var sValue = window.location.href.match(new RegExp("[\?\&]" + item + "=([^\&]*)(\&?)", "i"));
            return sValue ? sValue[1] : sValue;
      },
      downloadApp() {
        let downloadLink = this.pageData.apk_link;
        if (downloadLink) {
          window.location.href = downloadLink;
        } else {
          alert('当前设备不支持下载');
        }
      },
      // 点击抽奖按钮会触发star回调
      startCallback () {
        // 调用抽奖组件的play方法开始游戏
        this.$refs.myLucky.play()
        // 模拟调用接口异步抽奖
        setTimeout(() => {
          // 假设后端返回的中奖索引是0
          const index = 5
          // 调用stop停止旋转并传递中奖索引
          this.$refs.myLucky.stop(index)
        }, 3000)
      },
      // 抽奖结束会触发end回调
      endCallback (prize) {
        this.darkBtn = true;
        this.startCountdown('countdowntwo')
      }
    },
  };
  </script>
