    font-size: bold;
    margin:0 auto;
    cursor: pointer;
  }
  .tankuang{
    margin:10px auto;
    color:#fff;
    font-size:22px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    width:80%;
    text-align: center;
    padding:5px 10px;
    background: linear-gradient(0deg, #6633D5 0%, #9977F8 0%, #FE6ECF 50%, #FDDD69 100%);
  }
  .continuebtn.style2{
    background-size: 100% 100%;
    width: 250px;
    height: 70px;
    font-size: 14px;
  }
  .congratulation{
    font-weight: 900;
    font-size: 28px;
    text-align: center;
    font-style: normal;
    background: linear-gradient(0deg, #6633D5 0%, #9977F8 0%, #FE6ECF 50%, #FDDD69 100%);
    -webkit-background-clip:text;
    -webkit-text-fill-color:transparent;
  }
  .walmartAmazon{
    font-weight: 900;
    font-size: 28px;
    text-align: center;
    font-style: normal;
    background: linear-gradient(0deg, #6633D5 0%, #9977F8 0%, #FE6ECF 50%, #FDDD69 100%);
    -webkit-background-clip:text;
    -webkit-text-fill-color:transparent;
  }
  .van-nav-bar .van-icon{
    color:#111111;
  }
  .content{
    padding:15px;
    padding-bottom: 70px;
  }
  .headViewImg{
    display: flex;
    justify-content: space-between;
    position: fixed;
    top:0;
    left:0;
    width:calc(100% - 30px);
    padding:15px;
    background-color: #fff;
    z-index: 1;
  }
  .logoView{
    margin-top:5px;
  }
  .logoInfo{
    display: flex;
    .logoDec{
      margin-left:29px;
      flex:1;
    }
  }
  .logodecview{
    text-align: center;
    padding:0 11px;
    .logodecviewinfo{
      font-size: 18px;
      color:#202124;
    }
    .logodecviewdec{
      font-size: 17px;
      color:#5F6368;
      margin-top:11px;
    }
  }
  .installBtn{
    background: #0A5AD3;
    border-radius: 11px;
    padding:16px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    font-size: 18px;
    div{
      width: 100%;
      text-align: center;
    }
  }
 .flexcloum{
    display: flex;
    overflow-x: auto; /* 允许水平滚动 */
    scroll-behavior: smooth; /* 平滑滚动 */
    flex-wrap:nowrap;
    .select_type_view{
        border-radius: 8px;
        margin:0;
        flex: 0 0 auto; /* 不增长，不收缩，不占用多余空间 */
        margin-right: 10px; 
        width: 139px;
        height: 300px;
        background: #D9D9D9;
        border-radius: 10px;
        overflow: hidden;
    }
  }
  .details{
    .titleView{
      display: flex;
      justify-content: space-between;
      .titleHead{
        font-size: 24px;
      }
    }
    .detailsInfo{
      color:#5F6368;
      font-size: 18px;
      margin-top:30px;
    }
  }
  .tagsView{
    display: flex;
    margin-top:40px;
    >div{
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right:14px;
      padding:8px 15px;
      border-radius: 20px 20px 20px 20px;
      border: 1px solid #DDDEE1;
    }
    &.oneself{
      >div{
        font-size: 16px;
        padding:8px 10px;
        .van-image{
          margin-right:5px;
        }
        &.current{
          background: #ddeaff;
          color:#0A5AD3;
        }
      }
    }
  }
  .shareView{
    padding:26px;
    border-radius: 10px;
    border: 1px solid #DDDEE1;
    margin-top:40px;
  }
  .shareViewInfo{
    display: flex;
    margin-bottom:20px;
    &:last-child{
      margin-bottom: 0;
    }
  }
  .scoreView{
    display: flex;
    align-items: center;
    margin-top:15px;
  }
  .scoreNum{
    font-size: 75px;
    color:#202124;
  }
  .scoreproper{
    font-size: 16px;
    color: #5F6368;
  }
  .scoreStar{
    margin-bottom:10px;
  }
  .ratingLength{
    display: flex;
    width: 100%;
    align-items: center;
    margin-bottom:8px;
    &:last-child{
      margin-bottom:0;
    }
  }
  .ratingNum{
    color:#202124;
    font-size: 15px;
    margin-right:18px;
  }
  .ratingbg{
    width:90%;
    border-radius: 7px;
    height: 11px;
    background: #E3E3E3;
    position: relative;
    overflow: hidden;
    >div{
      position: absolute;
      top:0;
      left:0;
      background:#0A5AD3;
      height: 100%;
      border-radius: 7px;
    }
  }
  .commentView{
    margin-top:40px;
  }
  .commentHead{
    margin-bottom:10px;
    display: flex;
    justify-content: space-between;
  }
  .commentTime{
    margin-left:12px;
    font-size: 16px;
    color: #5F6368;
  }
  .commentContent{
    margin:10px 0;
    font-size: 16px;
    color: #5F6368;
  }
  .commentHeadImg{
    width:42px;
    height:42px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
    color:#fff;
  }
  .bottomLable,.bottomThreeLable{
    color:#202124;
    font-size:15px;
    margin:10px 0 15px 0;
  }
  .bottomThreeLable{
    display: flex;
    >div{
      margin-right:10px;
    }
  }
  .bottomViewFiexd{
    position: fixed;
    left:0;
    bottom:0;
    height: 55px;
    padding-top:5px;
    width:100%;
    background: #FFFFFF;
    display: flex;
    justify-content: space-around;
    box-shadow: 0px -4px 8px 0px rgba(0,0,0,0.1);
    >div{
      cursor: pointer;
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 12px;
      &:nth-of-type(2){
        span{
          color:#0A5AD3;
        }
      }
    }
  }
  </style>
  


