import Vue from 'vue';
import Router from 'vue-router';
import index from '../view/googlepllayshop/index';
Vue.use(Router);
export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    }
  ]
});



